@import url(https://fonts.googleapis.com/css2?family=Oleo+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
/* Google Fonts. */

body {
    margin: 0;
    background-color: #f8f8f8;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.title {
    font-size: 4em;
    font-family: 'Oleo Script', 'Georgia', serif;
    background-image: linear-gradient(to right, #249141, #249c8a, #079cb3, #3464eb);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;
    color: transparent;
}

.text-div {
    padding-left: 3%;
    font-family: 'Open Sans', sans-serif;
    font-size: 1.3em;
}

.artwork-div {
    height: 30em;
    margin-top: 2em;
    margin-bottom: 2em;
}

.artwork-image {
    /* Center. */
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: 30em;
}

/* General Kenobi image has special hover effect. */
#kenobi-image img {
    position: absolute;
    left: 50%;
    transform: translate(-50%,0);
    background-color: #f8f8f8;
    transition: opacity 1s ease-in-out;
}

#kenobi-image img.top:hover {
    opacity: 0;
}

#fancy-pants-image {
    height: 22em;
}

/* When user hovers over a navigation option, the text changes color,
   an underline appears, and the background darkens.
   When user is at a route, that navigation option text has the changed color. */

.nav-option {
    position: relative;
    display: inline-block;
    padding: 0.5em 1em 0.1em 1em;
    margin-bottom: 0.4em;
    line-height: 24px;
    height: 24px;
    color: black;
    text-decoration: none;
}

.nav-option::after {
    content: '';
    position: absolute;
    display: block;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 2px;
    border-bottom: 2px solid rgb(0, 131, 136);
}

.nav-option, .nav-option::after, .nav-option-div, .nav-option-div::after {
    transition: all 0.3s linear;
}

.nav-option:hover, .active-nav-option {
    color: rgb(0, 131, 136);
}

.nav-option:hover::after {
    left: 0;
    width: 100%;
}

.nav-option-div {
    display: inline-block;
}

.nav-option-div:hover {
    background-color: rgb(225, 227, 235);
}

#science-banner-image {
    width: 100%;
}

